import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import routers from '@src/routers'
import { Button, Layout } from 'antd'

function App() {
  return (
    <Layout>
      <Layout.Header>
        <span>Cherry</span>
        <Button>btn</Button>
      </Layout.Header>
      <Router>
        <Switch>
          {
            routers.map(r => <Route path={r.path} component={r.component} />)
          }
        </Switch>
      </Router>
    </Layout>

  );
}

export default App;
